import React, { useState, useEffect } from "react";
import transition from "../transition";
import imagem from "../assets/images/pessoas2.png";
import imagemResposta from "../assets/images/pessoasresposta.png";
import Swal from 'sweetalert2';
import { Link, useNavigate } from "react-router-dom";
import {
  ref as databaseRef,
  get,
  query,
  orderByChild,
  equalTo,
  update,
} from "firebase/database";
import { database } from "../firebase";


const Jogo3 = () => {
  const [tempoCarregamento, setTempoCarregamento] = useState(null);
  const [pontuacao, setPontuacao] = useState(0);
  const [tempo, setTempo] = useState(0);
  const [buttons, setButtons] = useState([
      { id: 2, text: "7", correct: false },
      { id: 3, text: "15", correct: false },
      { id: 1, text: "10", correct: false },
      { id: 4, text: "9", correct: true },
    { id: 5, text: "8", correct: false },
    { id: 6, text: "3", correct: false },
    // Adicione mais botes conforme necessrio
  ]);

  const navigate = useNavigate();

  useEffect(() => {
    // Registre o momento em que a pgina "Contact"  carregada
    const startTime = new Date().getTime();
    setTempoCarregamento(startTime);
  }, []);

  const handleCliqueNoLink = () => {
    if (tempoCarregamento) {
      // Registre o momento em que o link foi clicado
      const tempoClique = new Date().getTime();

      // Calcule o tempo decorrido em milissegundos
      const tempoDecorrido = tempoClique - tempoCarregamento;

      // Converta o tempo decorrido para segundos
      const tempoDecorridoSegundos = Math.round(tempoDecorrido / 1000);

      // Obtenha o nome do usurio do localStorage ou do Firebase
      const nomeUsuario = localStorage.getItem("nomeUsuario"); // ou de onde voc o obtm

      // Certifique-se de que voc tem um nome de usurio vlido
      if (nomeUsuario) {
        // Obtenha a pontuao atual do usurio
        const usuariosRef = databaseRef(database, "usuarios");
        const usuarioQuery = query(
          usuariosRef,
          orderByChild("nome"),
          equalTo(nomeUsuario)
        );

        get(usuarioQuery).then((snapshot) => {
          if (snapshot.exists()) {
            // Itere pelos filhos do snapshot para acessar o usurio
            snapshot.forEach((childSnapshot) => {
              const usuario = childSnapshot.val();
              if (usuario) {
                // Acesse a pontuao do usurio
                let pontuacaoAtual = usuario.pontuacao || 0; // Defina como 0 se for nulo

                // Calcule a nova pontuao somando a pontuao atual com o tempo decorrido
                let valorTotal = pontuacaoAtual + tempoDecorridoSegundos;

                setTempo(tempoDecorridoSegundos);

                // Atualize a pontuao no Firebase
                const usuarioKey = childSnapshot.key; // Obtenha a chave do usurio
                const atualizacao = {};
                atualizacao[`usuarios/${usuarioKey}/pontuacao`] = valorTotal;

                update(databaseRef(database), atualizacao)
                  .then(() => {
                    Swal.fire({
                      title: 'Sucesso!',
                      text: `Tempo registrado com sucesso! Sua nova pontuao  de ${valorTotal}.`,
                      icon: 'success',
                      confirmButtonText: 'OK',
                    }).then(() => {
                      // Redirecione o usurio para a prxima pgina aqui, se necessrio
                      // navigate("/ranking");
                      setPontuacao(1);
                    });
                  })
                  .catch((error) => {
                    console.error("Erro ao registrar tempo: ", error);
                  });
              } else {
                alert("Objeto de usurio  nulo.");
              }
            });
          } else {
            alert("Usurio no encontrado.");
          }
        });
      } else {
        alert("Nome de usurio no encontrado.");
      }
    } else {
      alert("Tempo de carregamento no registrado.");
    }
  };

  const handleButtonClick = (button) => {
    if (button.correct) {
      handleCliqueNoLink();
    } else {
      // Se o boto errado for clicado, substitua o boto por um smbolo de "X"
      setButtons((prevButtons) =>
        prevButtons.map((btn) =>
          btn.id === button.id ? { ...btn, text: "X" } : btn
        )
      );
    }
  };

  return (
    <>
      <div className="container">
        <div className="container_imagem_padrao">
          {pontuacao === 0 ? (
            <>
              <p style={{ color: "black", margin: '30px' }}>Encontre um livro vermelho na imagem a seguir</p>
              <img className="imagem" src={imagem} alt="imagem" />
              <div className="grid-botoes">
                {buttons.map((button) => (
                  <button
                    key={button.id}
                    className="bottoes"
                    onClick={() => handleButtonClick(button)}
                    disabled={button.text === "X"} // Desabilite botes com "X"
                  >
                    {button.text}
                  </button>
                ))}
              </div>
            </>
          ) : (
            <>
              <img className="imagem" src={imagemResposta} alt="imagem" />
              <p style={{ color: "black", marginTop: '30px' }}>Tempo que o Tensorflow demorou para identificar o objeto: 0.23 segundos</p>
              <p style={{ color: "black" }}>Seu tempo foi de {tempo} segundos</p>
              <Link to="/ranking">
                Prxima pgina
              </Link>
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default transition(Jogo3, "#b342ca");
